.tab-list {
  border-bottom: 1px solid #60a5fa;
  margin: 0 0 10px;
  padding: 0;
}

.tab--selected {
  background: #fff;
  border-color: #60a5fa !important;
  color: black;
  border-radius: 5px 5px 0 0;
  margin-right: 2px;
  margin-left: 2px;
}

.tab-style {
  display: inline-block;
  border: 1px solid #b6b6b6;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 5px 5px 0 0;
}

.load-form {
  list-style: none;
  margin: 0 auto;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  border: 2px 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  background: white;
}

.tab-div {
  margin: 0;
  overflow: hidden;
  background: white;
}

.invalid label,
.invalid p {
  color: red;
}

.invalid {
  background: #ffe6e6;
  box-shadow: 0 2px 8px rgb(239 0 0 / 26%);
}
